@import url(https://fonts.googleapis.com/css?family=Caveat|Dancing+Script|Righteous&display=swap);
@import url(https://fonts.googleapis.com/css?family=Caveat|Dancing+Script|Righteous&display=swap);
@import url(https://fonts.googleapis.com/css?family=Caveat|Dancing+Script|Righteous&display=swap);
@import url(https://fonts.googleapis.com/css?family=Caveat|Dancing+Script|Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  padding: 10px 40px;
  margin: 10px;
  font-size: 1em;
  font-weight: bolder;
  transition: 0.3s ease-in-out;
  font-family: "Caveat";
  background-color: rgb(216, 216, 216);
  border-radius: 50px;
  outline: none;
}

button:focus {
  outline: none;
}

button:hover {
  cursor: pointer;
}

.home button, .todos button, .view-todo button {
  color: white;
  border: solid 2px white;
  background-color: #222;
  transition: 0.5s ease-in-out;
}

.home button:hover, .todos button:hover, .view-todo button:hover {
  color: black;
  background-color: white;
  border: solid 2px #222;
}

h1 {
 font-family: "Righteous"
}

h3{
  font-family: "Dancing Script";
}

.home {
  top: 0;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background-color: #222;
  color: white
}

.flex-content {
 margin-top: -7%;
}

.mb-5 {
  margin-bottom: 35px;
}

.mb-10 {
  margin-bottom: 50px;
}
.view-todo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.view-todo-left, .view-todo-right {
  width: 50%;
}

.view-todo-left {
  text-align: justify;
  padding: 5%;
  background-color: #222;
  color: white;
  padding-top: 30%;
  height: 100%;
}

.view-todo-right button {
  width: 150px;
  font-size: 1.1em;
}

.view-todo-left p {
  margin-top: 30px;
  line-height: 1.2em;
  white-space: pre-line;
}

.view-todo-left button{
  margin-top: 10%;
}

.view-todo-right-button {
  position: fixed;
  width: 25%;
  margin-left: -10%;
}

.create-todo {
  top:0;
  position: absolute;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.left-div {
  width: 90%;
  position: relative;
  background-color: #222;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.right-div{
  width: 100%;
  position: relative;
  background-color: white;
}

.left-div-arrow {
  font-size: 2.5em;
  margin-left: 10px;
  margin-top: -5px;
  font-weight: bolder;
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
}

@-webkit-keyframes pulse {
  from { -webkit-transform : translateX(0em); transform : translateX(0em) }
  to   { -webkit-transform : translateX(1em); transform : translateX(1em) }
}

@keyframes pulse {
  from { -webkit-transform : translateX(0em); transform : translateX(0em) }
  to   { -webkit-transform : translateX(1em); transform : translateX(1em) }
}

form {
  padding-top: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 3%;
}

input, textarea{
  border: none;
  transition: 0.5s ease-in-out;
  min-width: 400px;
  width: 20%;
  padding: 10px
}

input:focus, textarea:focus {
  width: 30%;
  outline: none;
  border-bottom: solid 1px #222;

}

input[type=text] {
  border-bottom: 1px solid #222;
  margin-bottom: 20px;
  font-size: 1em;
  font-family: "Righteous";
}

textarea {
  border: 1px solid #222;
  min-height: 300px;
  min-width: 500px;
  margin-top: 15px;
  font-size: 0.9em;
  line-height: 1.9em;
  color: #555;
  margin-bottom: 20px
}

form button {
  background-color: white;
  border: solid 1.5px #222;
  min-width: 170px;
}

form button:hover {
  background-color: #222;
  color: white;
  cursor: pointer;
}

.main-div {
  top:0;
  position: absolute;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.left-div {
  width: 90%;
  position: relative;
  background-color: #222;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.right-div{
  width: 100%;
  position: relative;
  background-color: white;
}
.todo-card {
  display: -webkit-flex;
  display: flex;
  margin: 10px;
  border-bottom: 1px solid #333;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.todo-card h2{
  font-family: "Righteous";
}

.todo-description {
  margin-left: 30px;
  text-align: justify;
  width: 70%;

}

.todo-description p {
  font-family: "Caveat";
}

.todos .right-div {
  overflow: auto;
  padding: 0 2%;
}

.todos .left-div {
  width: 70%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.todos .left-div button {
  font-size: 1em;
}
.App {
  height: 100%;
  text-align: center;
}

a {
  text-decoration: none;
  color: #222;
}

a:hover {
  color: #555;
}

